/**
 * @file AxiosVue/
 */
// https://github.com/mzabriskie/axios
// import Vues from 'vue'
import axios from 'axios'
import qs from 'qs'
// import sesstionStorage from 'utils/session-storage'
import router from '../router'
// 
axios.defaults.baseURL = '/'
axios.interceptors.request.use((config) => {
  config.headers['X-Requested-With'] = 'XMLHttpRequest'
  // 
  // config.headers['Content-Type'] = 'multipart/form-data'
  // form
  config.headers['Content-Type'] = 'application/x-www-form-urlencoded'
  // jsonformdata
  config.transformRequest = [(data) => {
    return qs.stringify(data)
  }]
  return config
})

axios.interceptors.response.use((response) => {
  const routeUrl = response.request.responseURL
  const { data } = response
  const statusCode = data.code ? data.code : data.success

  switch (statusCode) {
    case 'success':
    case 'false':
    case 2001:
    case 2002:
      return data
    case 1003: // 
    case 200:
    case false:
    case true:
    case 'true':
      return data
    case 4010:
      return data
      // router.push({
      //   path: '/error/no-net',
      //   query: { redirect: data.message },
      // })
    case 401:
      //  nologin 
      if (!/nologin/ig.test(routeUrl)) {
        router.push({
          path: '/user/login',
          query: { redirect: router.currentRoute.fullPath },
        })
      }
      break

    default:
  }
  const err = new Error(data.description)
  err.code = data.code
  err.data = data
  err.response = response
  throw err
}, (err) => {
  if (err && err.response) {
    switch (err.response.status) {
      case 400:
        err.message = ''
        break

      case 401:
        err.message = ''
        break

      case 403:
        err.message = ''
        break

      case 404:
        err.message = `: ${err.response.config.url}`
        router.push({
          path: '/error/no-net',
          query: { redirect: router.currentRoute.fullPath },
        })
        break

      case 408:
        err.message = ''
        break

      case 500:
        err.message = ''
        router.push({
          path: '/error/no-net',
          query: { redirect: router.currentRoute.fullPath },
        })
        break

      case 501:
        err.message = ''
        router.push({
          path: '/error/no-net',
          query: { redirect: router.currentRoute.fullPath },
        })
        break

      case 502:
        err.message = ''
        router.push({
          path: '/error/no-net',
          query: { redirect: router.currentRoute.fullPath },
        })
        break

      case 503:
        err.message = ''
        router.push({
          path: '/error/no-net',
          query: { redirect: router.currentRoute.fullPath },
        })
        break

      case 504:
        err.message = ''
        router.push({
          path: '/error/no-net',
          query: { redirect: router.currentRoute.fullPath },
        })
        break

      case 505:
        err.message = 'HTTP'
        router.push({
          path: '/error/no-net',
          query: { redirect: router.currentRoute.fullPath },
        })
        break

      default:
    }
  }
  return Promise.reject(err)
})
axios.install = (Vue) => {
  Vue.prototype.$http = axios
}

export default axios
