<template>
  <div class="toast" :class="{ 'toast--active': active, 'toast--fade-in': fadeIn }" @transitionend="transitionend" v-html="message"></div>
</template>

<script  type="text/babel">
  const REGEXP_LINE_FEED = /\n/g

  export default {
    data() {
      return {
        /**
         * 
         * @type {boolean}
         */
        active: false,

        /**
         * 
         * @type {string}
         */
        message: '',

        /**
         * 
         * @type {Object}
         * @property {number} options.timeout - 
         */
        options: {},

        /**
         * 
         * @type {boolean}
         */
        fadeIn: false,
      }
    },
    props: {
      /**
       * 
       */
      timeout: {
        type: Number,
        default: 1000,
      },
    },
    methods: {
      /**
       * 
       * @public
       * @param {string} message - 
       * @param {Object} options - 
       * @return {Object} this
       */
      show(message, options = {}) {
        if (this.active || !message) {
          return this
        }

        this.active = true
        this.message = String(message).replace(REGEXP_LINE_FEED, '<br>')
        this.options = options
        this.$nextTick(() => {
          // 
          this.$el.offsetWidth // eslint-disable-line
          this.fadeIn = true
        })

        return this
      },

      /**
       * 
       * @private
       * emits Toast#shown
       */
      shown() {
        /**
         * 
         * @event Toast#shown
         */
        this.$emit('shown')
        setTimeout(this.hide, this.options.timeout || this.timeout)
      },

      /**
       * 
       * @public
       * @return {Object} this
       */
      hide() {
        this.fadeIn = false

        return this
      },

      /**
       * 
       * @private
       * emits Toast#hidden
       */
      hidden() {
        this.active = false
        this.message = ''
        this.options = {}

        /**
         * 
         * @event Toast#hidden
         */
        this.$emit('hidden')
      },

      /**
       * CSS Transition
       * @private
       */
      transitionend() {
        if (this.fadeIn) {
          this.shown()
        } else {
          this.hidden()
        }
      },
    },
  }
</script>


<style lang="stylus" rel="stylesheet/stylus" scoped>
@import '../../assets/stylus/variables.styl';

  .toast {
    position: fixed;
    display: none;
    width: 80%;
    padding: 30px;
    top: 50%;
    left: 50%;
    opacity: 0;
    background-color: rgba(0, 0, 0, .7);
    border-radius: 10px;
    color: #fff;
    text-align: center;
    transform: translate(-50%, -50%);
    z-index: $z-index-toast;
    transition: opacity .15s ease;

    &--active {
      display: block;
    }

    &--fade-in {
      opacity: 1;
    }
  }
</style>
