<template>
    <div class="checkbox-component">
        <input type="checkbox"
               class="checkbox-component__input"
               :id="id"
               :name="name"
               :value="value"
               :class="className"
               :disabled="disabled"
               @change="onChange"
               :checked="state">
        <label :for="id" class="checkbox-component__label">
          <span class="checkbox-component__indicator we-icon"></span>
            <slot></slot>
        </label>
    </div>
</template>

<script>
export default {
  name: 'we-checkbox',
  // v-model
  // https://cn.vuejs.org/v2/guide/components-custom-events.html
  model: {
    prop: 'modelValue', // v-model="paraValues"paraValuesmodelValue
    event: 'change', // input,paraValues
  },
  props: {
    /**
     * id
     */
    id: {
      type: String,
      default() {
        return `checkbox-id-${this._uid}`
      },
    },
    /**
     * name
     */
    name: {
      type: String,
      default: null,
    },
    /**
     * value
     */
    value: {
      required: true,
      default: null,
    },
    /**
     * modelprops
     */
    modelValue: {
      required: true,
      default: undefined,
    },
    /**
     * 
     */
    className: {
      type: String,
      default: null,
    },
    /**
     * 
     */
    checked: {
      type: Boolean,
      default: false,
    },
    /**
     * or
     */
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    /**
     * 
     * @return {boolean} modelValue false
     */
    state() {
      // false
      if (this.modelValue === undefined) {
        return this.checked
      }
      // 
      if (Array.isArray(this.modelValue)) {
        return this.modelValue.indexOf(this.value) > -1
      }
      return !!this.modelValue
    },
  },
  mounted() {
    if (this.checked && !this.state) {
      this.toggle()
    }
  },
  methods: {
    /**
     * CheckBox 
     */
    onChange() {
      this.toggle()
    },
    /**
     * modelValuevaluetrue or false
     * modelValuetruevaluefalsevalue
     */
    toggle() {
      let value
      if (Array.isArray(this.modelValue)) {
        value = this.modelValue.slice(0)
        if (this.state) {
          value.splice(value.indexOf(this.value), 1)
        } else {
          value.push(this.value)
        }
      } else {
        value = !this.state
      }
      // 
      this.$emit('change', value)
    },
  },
  watch: {
    /**
     * CheckBoxstatetoggle
     * @param {string} newValue - CheckBox false or true
    */
    checked(newValue) {
      if (newValue !== this.state) {
        this.toggle()
      }
    },
  },
}
</script>

