<template>
  <div class="dialog" :class="{ 'dialog--active': active, 'dialog--fade-in': fadeIn }" @transitionend="transitionend">
    <transition name="scale">
      <div class="dialog__content" v-if="active">
        <div class="dialog__header" v-if="options.title">
          <h5 class="dialog__title">{{ options.title }}</h5>
        </div>
        <div class="dialog__body" v-if="content" v-html="content" :style="options.style">
          <p></p>
        </div>
        <div class="dialog__footer">
          <button class="dialog__button dialog__button--default" data-action="default" type="button" v-if="options.default" @click="hide">{{ options.default }}</button>
          <button class="dialog__button dialog__button--primary" data-action="primary" type="button" v-if="options.primary" @click="hide">{{ options.primary || '' }}</button>
        </div>
      </div>
    </transition>
  </div>
</template>

<script  type="text/babel">
  const REGEXP_LINE_FEED = /\n/g

  export default {
    data() {
      return {
        /**
         * 
         * @type {string}
         */
        action: '',

        /**
         * 
         * @type {boolean}
         */
        active: false,

        /**
         * 
         * @type {string}
         */
        content: '',

        /**
         * 
         * @type {Object}
         * @property {string} options.title - 
         * @property {string} options.default - 
         * @property {string} options.primary - 
         * @property {Object} options.style - 
         */
        options: {},

        /**
         * 
         * @type {boolean}
         */
        fadeIn: false,
      }
    },
    methods: {
      /**
       * 
       * @public
       * @param {string} content - 
       * @param {Object} options - 
       * @return {Object} this
       */
      show(content, options = {}) {
        if (this.active || !content) {
          return this
        }

        this.active = true
        this.action = ''
        this.content = String(content).replace(REGEXP_LINE_FEED, '<br>')
        this.options = options
        this.$nextTick(() => {
          // 
          this.$el.offsetWidth // eslint-disable-line
          this.fadeIn = true
        })

        return this
      },

      /**
       * 
       * @private
       * emits Dialog#shown
       */
      shown() {
        /**
         * 
         * @event Dialog#shown
         */
        this.$emit('shown')
      },

      /**
       * 
       * @public
       * @param {Object} e - 
       * @return {Object} this
       */
      hide(e) {
        this.fadeIn = false
        this.action = e && e.target ? e.target.dataset.action : ''

        return this
      },

      /**
       * 
       * @private
       * emits Dialog#hidden
       */
      hidden() {
        this.active = false
        this.content = ''
        this.options = {}

        /**
         * 
         * @event Dialog#hidden
         */
        this.$emit('hidden', this.action)
      },

      /**
       * CSS Transition
       * @private
       */
      transitionend() {
        if (this.fadeIn) {
          this.shown()
        } else {
          this.hidden()
        }
      },
    },
  }
</script>

<style lang="stylus" scoped>
@import '../../assets/stylus/variables.styl'

.dialog {
  background-color: rgba(0, 0, 0, .5);
  bottom: 0;
  display: none;
  left: 0;
  opacity: 0;
  overflow: auto;
  position: fixed;
  right: 0;
  top: 0;
  transition: opacity .15s ease;
  z-index: 2010;

  &--active {
    display: block;
  }

  &--fade-in {
    opacity: 1;
  }

  &__content {
    background-color: #fff;
    border-radius: 4px;
    left: 50%;
    overflow: hidden;
    overflow-wrap: break-word;
    position: absolute;
    text-align: center;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    word-break: break-all;
  }

  &__header {
    padding: 40px 40px 20px;
  }

  &__title {
    font-size: 32px;
    margin-top: 0;
    margin-bottom: 0;
  }

  &__body {
    color: #999;
    font-size: 30px;
    padding: 0 40px 40px;
    text-align: left;
    line-height: 1.8;
    text-align center;
    &:first-child {
      color: #333;
      padding-top: 40px;
    }
  }

  &__footer {
    border-top: 1px solid #ececec;
    display: flex;
  }

  &__button {
    background-color: #fff;
    border: 0;
    cursor: pointer;
    display: block;
    flex: 1;
    font-size: 32px;
    line-height: 1.5;
    padding: 20px 40px;

    &:focus,
    &:active {
      background-color: #f4f4f4;
      outline: none;
    }

    &--disabled,
    &[disabled] {
      opacity: .5;
      cursor: not-allowed;
      pointer-events: none;
    }

    &--default {
      color: #333;
    }

    &--primary {
      color: $white;
      background-color: $dark;
    }

    & + & {
      border-left: 1px solid #ececec;
    }
  }
  .scale-enter-active{
    transform: translate(-50%, -50%) scale(0.8)
  }
  .scale-enter-to,
  .scale-leave-active  {
    transform: translate(-50%, -50%) scale(1)
    transition: transform .3s
  }
  .scale-leave-to {
    transform: translate(-50%, -50%) translateY(0.8)
  }
}
</style>
