import Toast from './toast.vue'

export default {
  /**
   * 
   * @param {Object} Vue - Vue
   * @param {Object} [pluginOptions] - 
   */
  install(Vue, pluginOptions = {}) {
    const VueToast = Vue.extend(Toast)
    const queues = []
    let toast = null

    /**
     * 
     * @param {Object} queue - 
     */
    function handle({ message, options, resolve }) {
      toast.$once('hidden', resolve)
      toast.show(message, options)
    }

    Vue.prototype.$toast = (message, options = {}) => {
      return new Promise((resolve, reject) => {
        if (!message) {
          reject(new Error(''))
          return
        }

        const queue = {
          message,
          options,
          resolve,
        }

        // Toast
        if (!toast) {
          toast = new VueToast()
          toast.$mount()
          document.querySelector(pluginOptions.container || 'body').appendChild(toast.$el)
        }
        if (toast.active) {
          queues.push(queue)
        } else {
          handle(queue)
        }
      }).then(() => {
        if (queues.length > 0) {
          handle(queues.shift())
        }
      })
    }
  },
}
